
export function profileRouter() {
    return [{
        path: '/profile',
        name: 'profile',
        component: resolve =>require(['./index.vue'],resolve),
        meta: {
            title: '个人中心'
        }
    },
    {
        path: '/profile/course',
        name: 'course',
        component: resolve =>require(['./components/course.vue'],resolve),
        meta: {
            title: '在线课程'
        }
    },
    {
        path: '/profile/courseDetail',
        name: 'courseDetail',
        component: resolve =>require(['./components/courseDetail.vue'],resolve),
        meta: {
            title: '详情'
        }
    },
    {
        path: '/profile/train',
        name: 'train',
        component: resolve =>require(['./components/train.vue'],resolve),
        meta: {
            title: '培训班级'
        }
    },
    {
        path: '/profile/exam',
        name: 'exam',
        component: resolve =>require(['./components/exam.vue'],resolve),
        meta: {
            title: '考试'
        }
    }]
}


